.todo_button {
    width: 250px;
    font-weight: bold;
    padding: 16px 25px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
      50deg,
      rgb(255, 255, 255) 0%,
      rgb(199, 199, 199) 100%
    );
    color: #000;
    text-transform: capitalize;
}